import axios from 'axios';

const state = {
	seasons: [],
  peoples: 1,
  roomSingle: 1,
  roomDouble: 0
};

const getters = {

  ROOM_SINGLE: state => {
    return state.roomSingle;
  },
  ROOM_DOUBLE: state => {
    return state.roomDouble;
  },

  /* eslint-disable no-unused-vars */

  RESULT: (state, getters) => {
    
  }

  /* eslint-enable no-unused-vars */
  
};

const mutations = {
	SET_SEASONS: (state, payload) => {
    state.seasons = payload;
	},
  SET_PEOPLES: (state, payload) => {
    state.peoples = payload;
  }
};

const actions = {
	SET_SEASONS: async (context) => {
		await axios({
			method: 'post',
			headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
			url: 'http://safari.post/',
			data: {
				'action': 'get_season'
			}
		}).then(({data}) => {
			context.commit('SET_SEASONS', data);
		})
	},
  SET_PEOPLES: (context, payload) => {
    context.commit('SET_PEOPLES', payload);
  }
};

export default {
  state,
  getters,
  mutations,
  actions,
};